html {
  --lite-blue: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  background-color: #ddd;
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  position: relative;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  height: 100%;
}

.nav__list {
  flex-direction: row;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  text-align: center;
  height: 100%;
  display: flex;
}

.nav__item {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 100%;
  margin-left: 20px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.nav__item--laptops:hover {
  color: var(--lite-blue);
}

.is-active {
  color: var(--lite-blue);
  margin-left: 0;
}

.is-active:after {
  background-color: var(--lite-blue);
  content: " ";
  border-radius: 2px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

